'use strict';
import React from 'react';
import ReactDOM from 'react-dom';
import { default as __links } from 'sources/links';

require('styles/pages/contact.less');

const ContactLink = React.createClass({
  render() {
    return (
      <li className="link-item list-item-icon">
        <div className="list-item-icon__icon">
          <a href={ this.props.url } target="_blank"><i className={`icon ${this.props.icon}`}></i></a>
        </div>
      </li>
    );
  }
});


const ContactLinks = React.createClass({
  render() {
    return (
      <ul className="link-items list-icons animated fadeIn">
        { this.props.links.map((result) => {
          <ContactLink key={ result.name } url={ result.url } icon={ result.icon } />
        }) }
      </ul>
    );
  }
});


export default React.createClass({
  getDefaultProps() {
    return {
      links: __links,
      backdrop: require( 'images/site/banana-plants.png')
    };
  },
  componentDidMount() {
    if (this.isMounted()) {
      ReactDOM.render(<ContactLinks { ...this.props } />, document.getElementById('contact'));
    }
  },
  render() {
    return (
      <div style={ {backgroundImage: `url(${this.props.backdrop})`} } className="page-wrapper" id="contact">

      </div>
    );
  }
});


